import { Router } from "express";

const router: Router = Router();

router.get('/', (req, res) => {
  try {
    res.status(200).json({ message: 'productos' });
  } catch (error) {
    res.status(500).json({ error: `Error en el servidor: ${error}` });
  }
})

router.post('/')

router.put('/')

router.delete('/')

export default router;
